





































































































import Vue, { PropType } from 'vue'

export default Vue.extend({
  props: {
    waveform: String,
    sustainMethod: String as PropType<"momentary" | "exclusive" | "alternative">,
    toneRange: Number,
    isMajor: Boolean,
    musicalKey: String,
    dark: Boolean
  },
  data: () => ({
    waveforms: {
      title: '波形',
      select: [
        {waveform: "sine", text: '正弦波'},
        {waveform: "square", text: '方形波'},
        {waveform: "triangle", text: '三角波'},
        {waveform: "sawtooth", text: '鋸波'}
      ]
    },
    sustainMethods: {
      value: 'momentary',
      items: {
        momentary: '瞬時',
        exclusive: '排他',
        alternative: '交互'
      }
    },
    scale: {
      title: '調',
      musicalKey: {
        items: ['平均律', 'A', 'Bb', 'B', 'C', 'C#', 'D', 'Eb', 'E', 'F', 'F#', 'G', 'Ab']
      },
      scale: {
        items: {
          major: {
            title: '長調',
            value: true,
          },
          minor: {
            title: '短調',
            value: false,
          },
        }
      }
    },
    toneRanges: {
      value: 2,
      items: ["低1", "低2", "中", "高1", "高2"]
    }
  })
})
